import logo from './logo.svg';
import './App.css';

import React,{useState} from "react";

import Header from './Header';
import {BrowserRouter as Router} from "react-router-dom";
import Routes from './Routes';
import productItems from './Data';

const App=()=>
{
  const [cartItems,setCartItems]= useState([]);

  const handleAddProducts=(product)=>
  {
    const ProductExist=cartItems.find((item)=>item.id===product.id);
    if(ProductExist){
      setCartItems(cartItems.map((item)=>item.id===product.id ? {...ProductExist,quantity:ProductExist.quantity+1}:item));
    }

      else
      {
        setCartItems([...cartItems,{...product,quantity:1}]);
      }
    }

    const handleRemoveProducts=(product)=>
    {
      const ProductExist=cartItems.find((item)=>item.id===product.id);
      if(ProductExist.quantity===1)
      {
        setCartItems(cartItems.filter((item)=>item.id!==product.id));
      }

      else
      {
        setCartItems(
        cartItems.map((item)=>item.id===product.id ? {...ProductExist,quantity:ProductExist.quantity-1}
        :item
        )
        );

      }
    }

    const handleClearance=()=>
    {
      setCartItems([]);
    }
  
  
  return <div>
    <Router> 
    <Header  cartItems={cartItems}/>
    <Routes productItems={productItems} 
    cartItems={cartItems}
     handleAddProducts={handleAddProducts}
     handleRemoveProducts={handleRemoveProducts}
     handleClearance={handleClearance} />
    </Router>
  </div>
}
export default App;
