import { useState } from "react";
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
 

export function Counter() {
    //hooks-useState -name starts with'use' -takes initial value of the stae
    const [like, setLike] = useState(0);
    const [dislike, setDislike] = useState(0);
    // setLike- which when called React listen to it and updates like
    //  state-CURRENT value of all the variables in REACT
    // like is the number setlike is the function
    const incrementLike = () => setLike(like + 1);
    const incrementdlike = () => setDislike(dislike + 1);
  
    const styles = {
      color: "black",
    };
    return (
  
      <div>
  
        <IconButton style={styles} onClick={incrementLike} aria-label="like">
          <Badge badgeContent={like} color="secondary">
            üëç
          </Badge>
        </IconButton>
  
        <IconButton style={styles} onClick={incrementdlike} aria-label="dislike ">
          <Badge badgeContent={dislike} color="error">
            üëé
          </Badge>
  
        </IconButton>
  
        {/* <button onClick={incrementLike }>üëç{like}</button> */}
        {/* <button onClick={incrementdlike }>üëé{dislike}</button> */}
        {/* <p>{like}</p> */}
      </div>
    );
  }