import React from "react";
import { Counter } from "./Counter";
import "./Products.css";



const Products=({productItems,handleAddProducts,cartItems})=>


{
    const isDisable=(product)=>
{
    const ProductExist=cartItems.find((item)=>item.id===product.id);
    if(ProductExist)
    {
        return true;
    }
    else
    {
        return false;
    }
}
    return(
        <div className="products">
            {productItems.map((productItem)=>(
                <div className="card">
                    <div>
                        <img className="img" src={productItem.img}></img>
                    </div>
                    <div>
                        <h3 className="product-name">
                        {productItem.name}
                        </h3>
                    </div>

                    <div className="price">
                        INR  <b> {productItem.price}</b>
                        
                    </div>

                    <div className="rating">
                        <Counter />
                        </div>

                    <div>
                        <button className="button"  disabled={isDisable(productItem)}onClick={()=>handleAddProducts(productItem)}>Add to Cart</button>
                    </div>
                    </div>
            ))}
        </div>
    )
}

export default Products;