import React from "react";
import productItems from "./Data";
import "./Cart.css"

const Cart=({cartItems,handleRemoveProducts,handleAddProducts,handleClearance})=>
{
    const totalPrice=cartItems.reduce((price,item)=>price + item.quantity * item.price,0 );
    return(
        <div className="cart-items">
            <h2 className="cart-items-header">
                Cart Items
            </h2>

            <div className="clear-cart">
                {cartItems.length >=1 &&(
                    <button className="clear-cart-button" onClick={()=>handleClearance()}>
                        Clear Cart
                    </button>
                )}
            </div>

            {cartItems.length ===0 &&(

              <div className="cart-items"> No Items Added</div>)}

            
                    {cartItems.map((item)=>
                    (
                        <div key={item.id} className="cart-items-list">
                            <div>
                            <img 
                            className="cart-items-image" 
                            src={item.img}></img>
                            </div>

                            <div className="cart-items-name"><h5>{item.name}</h5></div>
                            <div className="cart-items-function">
                                <button className="cart-item-remove" onClick={()=>handleRemoveProducts(item)}>
                                    Remove
                                </button>
                            </div>

                            <div className="cart-items-price">
                                 {item.price}
                            </div>
                            </div>

                    ))}
             

             <div className="cart-items-total-price">
                 <b>Total Price:    </b>
                 <div>
                   <b>  INR</b> {totalPrice}
                 </div>
             </div>
            
        </div>


    )
}



export default Cart;