import React from "react";
import { Route, Switch } from "react-router-dom";
import Products from './Products';
import Cart from './Cart'


const Routes=({productItems,cartItems,handleAddProducts,handleRemoveProducts,handleClearance})=>
{
    return (
    <div>
        <Switch>
            <Route path="/" exact>
                <Products productItems={productItems} cartItems={cartItems} handleAddProducts={handleAddProducts} />
            </Route>
            <Route path="/cart" exact>
                <Cart cartItems={cartItems}
                 handleAddProducts={handleAddProducts} 
                 handleRemoveProducts={handleRemoveProducts}
                 handleClearance={handleClearance}/>
            </Route>
        </Switch>
    </div>
    );
}

export default Routes;